:root {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  --jet-black: #2A2B2A;
  --white: #ffffff;
  --uranian-blue: #BDE0FE;
  --grey: #b9b9b9;
  --thistle: #CDB4DB;
  --fairy-tale: #FFC8DD;
  --mint-green: #5DFFD9;
}

h1, h2, h3, h4, h5, figure, p, ol, ul {
  margin: 0;
}

ol[role="list"], ul[role="list"] {
  list-style: none;
  padding-inline: 0;
}

h1, h2, h3, h4, h5 {
  font-size: inherit;
  font-weight: inherit;
}

img {
  display: block;
  max-inline-size: 100%;
}

input, button, textarea, select {
  font: inherit;
  font-size: 100%;
  width: 150px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

img {
  height: 500px;
  width: auto;
}

header {
  display: flex;
  justify-content: space-between;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

input[type='checkbox'] {
  appearance: none;
  height: 1.3rem;
  width: 1.3rem;
  margin: 0;
  padding: 0;
  border: solid var(--grey) 2px;
  border-radius: 100px;
  position: relative;
  background-color: transparent;
  cursor: pointer;
}

input[type='checkbox']:checked {
  background-color: var(--grey);
}

input[type='checkbox']:checked::after {
  content: "";
  position: absolute;
  left: 0.4rem;
  top: 0.15rem;
  width: 0.3rem;
  height: 0.6rem;
  border: solid var(--white);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

textarea {
  resize: none;
  width: 100%;
  padding: 0px 5px 0px 5px;
}

* {
  box-sizing: border-box;
}


.bold {
  font-weight: 600;
}

.remove-border-background {
  background: none;
  border: none;
}

.pointer {
  cursor: pointer;
}

.layout {
  display: flex;
}

.sidebar {
  position: fixed;
  height: 100vh;
  width: 320px;
  display: flex;
  flex-direction: column;
  flex: 1;
  color: var(--jet-black);
  background-color: var(--uranian-blue);
  font-size: 1.4rem;
  padding: 1rem;
  border-right: solid 4px var(--jet-black);
  z-index: 1000;
}

nav {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.logo-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
  height: 100px;
  margin: 0px 0 45px 0;
}

.logo {
  height: 100%;
  width: auto;
}

.logo-wrapper::after {
  content: "";
  position: absolute;
  bottom: -27px;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  height: 4px;
  background-color: var(--jet-black);
  border-radius: 20px;
}

.links {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 0px;
}

.nav-icon {
  height: 1.6rem;
  align-self: center;
  padding: 0;
  margin: 0;
}

.nav-home > .nav-home-icon,
.nav-new-project > .new-project-icon {
  margin-right: 12px;
}

.nav-home {
  display: flex;
  margin-bottom: 20px;
}

.home-button {
  font-size: 1.6rem;
}

.nav-new-project {
  display: flex;
  margin-bottom: 20px;
}

.project-container {
  display: grid;
  grid-template-columns: 1.8rem auto;
  grid-template-rows: 1.8rem auto;
  column-gap: 10px;
  row-gap: 2px;
  align-items: center;
  margin-bottom: 20px;
}

.active-project {
  outline: dotted 3px var(--white);
  outline-offset: 5px;
  border-radius: 0.5rem;
}

.nav-project-icon {
  display: flex;
  height: 1.4rem;
}

.nav-project-desc {
  grid-column-start: span 2;
  font-size: 1rem;
}

.sidebar-icons {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.icon {
  width: 50px;
  height: auto;
}

.main {
  margin-left: 320px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex: 5;
  font-size: 3.5rem;
}

.page {
  display: flex;
  flex-direction: column;
  min-width: 700px;
}

.header {
  display: flex;
  position: sticky;
  top: 0;
  background-color: var(--white);
  z-index: 999;
  padding: 20px 0px 20px 30px;
  margin-bottom: 20px;
}

.title-icon {
  height: 4.5rem;
  margin: 5px 10px 0 0;
}

.page-title {
  color: var(--jet-black);
}

.header::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
  width: 95%;
  height: 4px;
  background-color: var(--jet-black);
  border-radius: 20px;
}

.content {
  overflow-y: auto;
  flex-grow: 1;
  padding: 0px 0px 50px 50px;
}

.project-description {
  font-size: 1.4rem;
  width: 50%;
  color: var(--grey);
}

.edit-input {
  font-family: inherit;
  font-size: 1rem;
  color: var(--jet-black);
  background-color: var(--white);
  border: 2px solid var(--grey);
  border-radius: 5px;
  outline: none;
  width: auto;
  min-width: 0;
}

textarea.desc-edit-input {
  margin: 0;
  padding: 0 6px;
  font-size: 1.4rem;
  line-height: 1.4;
  vertical-align: top;
  align-self: flex-start;
  box-sizing: border-box;
}

.desc-edit-input {
  max-width: 50%;
  font-size: 1.4rem;
  margin-bottom: 0px;
  padding: 4px 6px;
  margin: 0 0 50px 0;
}

.new-list-container {
  display: flex;
  gap: 20px;
  width: 200px;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  background-color: var(--mint-green);
  border-radius: 25px;
  margin: 20px 0 20px 0;
}

.new-list-button,
.delete-list-button {
  display: flex;
  height: 1.8rem;
  width: 1.8rem;
  align-self: center;
}

.empty {
  font-size: 1.6rem;
}

.section {
  margin-bottom: 70px;
  position: relative;
}

svg {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 0;
  margin: 0;
}

.section::after {
  content: "";
  position: absolute;
  bottom: -40px;
  width: 30%;
  height: 4px;
  background-color: var(--grey);
  border-radius: 20px;
}

.heading-container {
  display: flex;
  align-items: center;
  padding-bottom: 8px;
  gap: 10px;
}

.delete-list-button,
.new-todo-button {
  display: flex;
  height: 1.6rem;
  width: 1.6rem;
  align-self: center;
  margin-top: 7px;
}

.heading {
  font-size: 2rem;
}

.list-item-container {
  box-sizing: border-box;
  width: auto;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  gap: 15px;
}

.list-edit-input {
  font-size: 2rem;
  width: auto;
  max-width: 100%;
  padding: 0px 8px;
}

.todo-date,
.todo-priority {
  font-size: 1rem;
  padding: 0px 5px;
  border-radius: 5px;
}

.todo-date {
  background-color: var(--thistle);
}

.todo-priority {
  background-color: var(--fairy-tale);
  padding: 0px 8px;
}

.completed-todo-text {
  text-decoration: line-through;
  color: var(--grey)
}

.completed-todo-details {
  text-decoration: line-through;
  color: var(--grey);
  background-color: var(--mint-green);
}

.new-todo-button svg,
.delete-list-button svg,
.delete-todo-button svg {
  fill: var(--white);
  transition: fill 0.5s ease;
  cursor: pointer;
}

.heading-container:hover .new-todo-button svg,
.heading-container:hover .delete-list-button svg,
.list-item-container:hover .delete-todo-button svg {
  fill: var(--jet-black);
}

.todo-edit-label-input {
  font-size: 1.3rem;
  width: auto;
  max-width: 100%;
  min-width: 50px;
  padding: 1px 10px;
}

.edit-input:focus {
  border-color: var(--uranian-blue);
  box-shadow: 0 0 0 2px rgba(189, 224, 254, 0.5);
}

.section ul li {
  margin-bottom: 10px;
}

.new-project-dialog,
.new-list-dialog,
.new-todo-dialog {
  position: fixed;
  top: 25%;
  left: calc(320px + (((100vw / 6) * 5) / 2));
  transform: translate(-50%);
  width: 500px;
  padding: 10px 20px 25px 20px;
  margin: 0;
  font-size: 1.2rem;
  background-color: var(--thistle);
  color: var(--jet-black);
  border: solid 4px var(--jet-black);
  border-radius: 20px;
  z-index: 2000;

}

.new-project-form-heading,
.new-list-form-heading,
.new-todo-form-heading {
 font-size: 2rem;
 text-align: center;
}

fieldset {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}

.form-row {
  width: 100%;
  padding: 0;
  margin-bottom: 15px;
}

label {
  font-size: 1.4rem;
  display: block;
}

input {
  width: 100%;
  padding: 0 5px 0 5px;
  font-size: 1.4rem;
}

.new-project-form-buttons,
.new-list-form-buttons,
.new-todo-form-buttons {
  display: flex;
  justify-content: space-around;
}

.new-project-form-buttons > button,
.new-list-form-buttons > button,
.new-todo-form-buttons > button {
  height: 35px;
  width: 110px;
}

button {
  border-radius: 10px;
  cursor: pointer;
  background-color: var(--white);
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: var(--grey);
}

::backdrop {
  background: rgba(0, 0, 0, 0.3);
}


